<template>
  <div id="app">
    <div class="task-list">
      <h1 class="title">Task Management App</h1>

      <button class="btn-toggle-form" @click="toggleForm">
        Show Add Task Form
      </button>

      <form v-if="showForm" @submit.prevent="addTask" class="form">
        <input
          type="text"
          v-model="taskInput"
          class="task-input"
          placeholder="Enter task name"
        />
        <button type="submit" class="btn-add">Add Task</button>
      </form>

      <p v-if="tasks.length === 0" class="no-tasks">No tasks available</p>

      <ul>
        <li
          v-for="(task, index) in tasks"
          :key="index"
          :class="['task-item', { completed: task.completed }]"
        >
          {{ task.name }}
          <div class="task-actions">
            <button
              class="btn-completed"
              @click="toggleCompletion(index)"
              v-if="!task.completed"
            >
              Complete
            </button>
            <button
              class="btn-completed"
              @click="undoCompletion(index)"
              v-if="task.completed"
            >
              Undo
            </button>
            <button class="btn-delete" @click="deleteTask(index)">
              Delete
            </button>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tasks: [],
      taskInput: "",
      showForm: false,
    };
  },
  created() {
    this.loadTasks();
  },
  methods: {
    toggleForm() {
      this.showForm = !this.showForm;
    },
    addTask() {
      if (this.taskInput.length >= 3) {
        this.tasks.push({ name: this.taskInput, completed: false });
        this.taskInput = "";
        this.saveTasks();
      } else {
        alert("Task name must be at least 3 characters long.");
      }
    },
    toggleCompletion(index) {
      this.tasks[index].completed = !this.tasks[index].completed;
      this.saveTasks();
    },
    undoCompletion(index) {
      this.tasks[index].completed = false;
      this.saveTasks();
    },
    deleteTask(index) {
      this.tasks.splice(index, 1);
      this.saveTasks();
    },
    saveTasks() {
      localStorage.setItem("tasks", JSON.stringify(this.tasks));
    },
    loadTasks() {
      const savedTasks = localStorage.getItem("tasks");
      if (savedTasks) {
        this.tasks = JSON.parse(savedTasks);
      }
    },
  },
};
</script>
